import {motion} from 'framer-motion'
const FoodCard = ({foodImg ,variants,foodName,foodPrice,isAvailable,onAdd}) => {
    console.log(onAdd);
    return (
      <motion.div
        variants={variants}
        initial="hidden"
        animate="visible"
        className="foodCard"
      >
        <img className="food-img" alt="" src={foodImg} />
        <p className="food-title">{foodName}</p>
        <p className="food-price">{foodPrice}</p>
        <p className="available">{isAvailable}</p>
        {/* <button onClick={onAdd}>+++</button> */}
      </motion.div>
    );
};



export default FoodCard;