import {useState} from 'react'
import {
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom';

import HomePage from './pages/HomePage'
import DashPage from './pages/DashboardPage'
import SettingsPage from './pages/Settings'
import Sidebar from './containers/Sidebar/Sidebar'
import './assets/scss/main2.scss'
import "antd/dist/antd.css";
import './assets/main.scss'


function App() {

const [dishItem,setDishItem] = useState([]);

  const onAdd = (coldDishes) =>{
    const exist = dishItem.find((x)=> x.id === coldDishes.id)
    if(exist){
      setDishItem(
        dishItem.map((x)=>
        x.id === coldDishes.id ? {...exist,qty:exist.qty+1} : x
        )
      );
    }else{
      setDishItem([...dishItem,{...coldDishes,qty:1}]);
    }
  }
  return (
    <Router>


      <div className='content'>
        <Sidebar />
        <Switch>
          <div className='container'>
              <Route exact path="/" >
                <HomePage onAdd={onAdd}/>
              </Route>
              <Route path="/dashboard" component={DashPage} />
              <Route path="/settings" component={SettingsPage} />
          </div>
        </Switch>
      </div>


    </Router>
  );
}

export default App;
