import './order.css'
import ButtonOrder from '../../components/Order buttons/orderButton'
import { useState } from 'react'

import OrderTotal from '../../components/order total/orderTotal'
import SelectedItem from '../../components/SelectedItem/SelectedItem'
import {soup} from '../../data/Dishes'

const Order = ({setDrawerState, drawerState}) => {

    const [btnState, setBtnState] = useState('Dine In')

    return (
      <div className="order-box">
        <h1 className="order-info">Orders #34562</h1>
        <div className="order-btns">
          <ButtonOrder
            buttonActive={btnState}
            setBtnState={setBtnState}
            buttonInfo="Dine In"
          />
          <ButtonOrder
            buttonActive={btnState}
            setBtnState={setBtnState}
            buttonInfo="To Go"
          />
          <ButtonOrder
            buttonActive={btnState}
            setBtnState={setBtnState}
            buttonInfo="Delivery"
          />
        </div>
        <div className="row">
          <div>
            <p className="order-items">Item</p>
          </div>
          <div className="row-order-left">
            <p className="order-items">Qty</p>
            <p className="order-items">Price</p>
          </div>
        </div>
        <div className="zakaz">
          {soup.slice(0, 2).map((item) => (
            <SelectedItem img={item.soupImg} quantity="1" price={item.soupPrice} key={item.id} />
          ))}
        </div>
        <div>
          <OrderTotal
            drawerState={drawerState}
            setDrawerState={setDrawerState}
          />
        </div>
      </div>
    );
}

export default Order;